.profile__avatar-edit{
    margin: 0;
    padding: 0;
    position: absolute;
    width: 120px;
    height: 120px;
    background: none;
    border: none;
    border-radius: 50%;
}

.profile__avatar-edit:hover {
    cursor: pointer;
    background: rgba(0, 0, 0, 0.8) url(../../../images/profile__avatar-edit.svg) no-repeat center;
}